@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: 'Roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #ccc;
  background-color: rgb(20, 29, 46);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: white;
  text-decoration: none;
}
img {
  width: 100%;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 0 auto;
  width: 100%;
  max-width: 1440px;
}
.root {
  position: relative;
}

.header {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.header__main {
  padding: 30px 50px;
}
.header__content {
  background-image: url(/react-app/static/media/bg.cbecb717.png);
  background-repeat: no-repeat;
  background-size: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 128px;
}
.header__title {
  -webkit-filter: drop-shadow(0 0 3px rgba(100, 50, 150, 50));
          filter: drop-shadow(0 0 3px rgba(100, 50, 150, 50));
  font-size: 5em;
}
.hello {
  color: tomato;
}
.header__subtitle {
  background-color: rgba(255, 99, 71, 0.5);
  font-weight: bold;
  max-width: 400px;
  font-size: 2em;
  text-align: right;
}
.navigation {
  margin-bottom: 100px;
  position: fixed;
  left: 0;
  width: 100%;
  padding: 15px 0;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    180deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(20, 29, 46, 1) 91%,
    rgba(0, 212, 255, 1) 100%
  );
  z-index: 100;
}
.navigation__list {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.navigation__item {
  display: block;
  font-size: 1.2em;
  text-transform: uppercase;
}
.navigation__item > a {
  display: block;
}
.navigation__item:hover {
  text-decoration: underline;
}

@media (max-width: 527px) {
  .navigation {
    margin-bottom: 64px;
  }
  .navigation__item {
    font-size: 1em;
  }
  .header__content {
    margin-top: 64px;
  }
  .header__main {
    text-align: center;
    padding: 1em 1em;
  }
  .header__title {
    font-size: 3em;
  }
  .header__subtitle {
    position: relative;
    text-align: center;
    font-size: 1.5em;
    padding: 1em 1em;
  }
  .header__subtitle::before {
    position: absolute;
    text-align: left;
    content: '';
    width: 70%;
    height: 2px;
    background-color: brown;

    margin-bottom: 1em;
  }
}

.skills__title {
  padding: 40px;
  font-size: 4em;
  text-align: center;
}
article.accordion {
  display: block;
  width: 31em;
  margin: 0 auto;
  background-color: rgba(255, 250, 240, 0.05);
  overflow: auto;
  border-radius: 5px;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.3);
}
article.accordion section {
  position: relative;
  display: block;
  float: left;
  width: 2em;
  height: 12em;
  margin: 0.5em 0 0.5em 0.5em;
  color: black;
  background-color: white;
  overflow: hidden;
  border-radius: 3px;
}
article.accordion section h2 {
  position: absolute;
  font-size: 1em;
  font-weight: bold;
  width: 12em;
  height: 2em;
  top: 12em;
  left: 0;
  text-indent: 1em;
  padding: 0;
  margin: 0;
  color: black;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
}
article.accordion section h2 a {
  display: block;
  width: 100%;
  line-height: 2em;
  text-decoration: none;
  color: inherit;
  outline: 0 none;
}
article.accordion section:target {
  width: 20em;
  padding: 0 1em;
  color: #333;
  background-color: rgba(255, 255, 255, 0.103);
}
article.accordion section:target h2 {
  position: static;
  font-size: 1.3em;
  text-indent: 0;
  color: #fff;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}
article.accordion section,
article.accordion section h2 {
  transition: all 1s ease;
}
ul {
  color: white;
}
@media (max-width: 528px) {
  article.accordion {
    width: 19em;
  }
  article.accordion section:target {
    width: 8em;
  }
} ;

/* main styles */
.main {
  width: 100%;
  /* background-color: rgb(32, 9, 54); */
  margin-bottom: 50px;
}
.main__title {
  text-align: center;
  font-size: 5em;
  padding: 40px;
}
.main__list {
  max-width: 300px;
  margin: 0 auto;
  padding: 5px;
}
.main__list > li {
  position: relative;
  font-size: 2em;
  margin-bottom: 1em;
  border-bottom: 9px solid tomato;
  box-shadow: 5px 5px 10px 5px #000;
  transition: 0.3s;
}
.main__list > li:nth-child(1):after {
  position: absolute;
  bottom: -7px;
  left: 2px;
  width: 98%;
  content: '';
  height: 5px;
  background-color: rgb(0, 255, 128);
  -webkit-animation: 3s skillsForHtml ease-in-out;
          animation: 3s skillsForHtml ease-in-out;
}
.main__list > li:nth-child(2):after {
  position: absolute;
  bottom: -7px;
  left: 2px;
  content: '';
  height: 5px;
  width: 90%;
  background-color: springgreen;
  -webkit-animation: 3s skillsForCss ease-in-out;
          animation: 3s skillsForCss ease-in-out;
}
.main__list > li:nth-child(3):after {
  position: absolute;
  bottom: -7px;
  left: 2px;
  content: '';
  height: 5px;
  width: 50%;
  background-color: springgreen;
  -webkit-animation: 3s skillsForJs ease-in-out;
          animation: 3s skillsForJs ease-in-out;
}
.main__list > li:nth-child(4):after {
  position: absolute;
  bottom: -7px;
  left: 2px;
  content: '';
  height: 5px;
  width: 15%;
  background-color: springgreen;
  -webkit-animation: 3s skillsForReact ease-in-out;
          animation: 3s skillsForReact ease-in-out;
}

/* media max wigth */
@media (max-width: 527px) {
  .main__title {
    font-size: 3em;
    text-align: center;
  }
  .main__subtitle {
    font-size: 1.5em;
    text-align: center;
    padding: 1em 1em;
  }
}
/* media min width */
@media (min-width: 568px) {
  .main {
    margin-bottom: 130px;
  }
}
@media (min-width: 815px) {
  .main__list {
    max-width: 700px;
  }
}

